
import { Space, Button } from 'ant-design-vue'
import { store } from '@ice/stark-data'

let router: any

store.on(
  'router',
  (e: any) => {
    router = e
  },
  true
)

export default {
  name: 'NavigateView',
  data() {
    return {
      urls: [
        {
          label: '跳转至主应用 page1',
          path: '/main/communication-test',
        },
        {
          label: '跳转至Vue2子应用',
          path: '/vue2App/communication-test',
        },
        {
          label: '跳转至React18子应用',
          path: '/reactApp/communication-test',
        },
        {
          label: '跳转至Vite子应用',
          path: '/viteApp/communication-test',
        },
      ],
    }
  },
  methods: {
    handleClick(path: string) {
      router?.push(path)
    },
  },
  components: {
    [Space.name]: Space,
    [Button.name]: Button,
  },
}
